* {
	font-family: 'Roboto', sans-serif; }
header {
	position: relative;
	height: 50px; }
nav {
	height: 50px;
	background-color: #212121;
	position: fixed;
	width: 100%;
	z-index: 5; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none; }
.menu_link {
	color: #ffffff;
	font-style: 15px;
	font-weight: 300; }
.menu_item {
	border-right: 2px dotted black;
	padding-right: 41px; }
.subheader {
	top: 50px;
	position: absolute;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6); }
.subheader_logo {
	display: block;
	min-width: 100%;
	margin-top: 16px; }
.subheader_official {
	margin-top: 6px;
	color: #ffffff;
	font-style: 13px;
	font-weight: 400;
	opacity: 0.5; }
.subheader_call {
	color: #ffffff;
	font-size: 12px;
	font-weight: 300;
	margin-top: 19px;
	text-align: right; }
.subheader_phone {
	display: block;
	margin-top: 8px;
	color: #ffffff;
	font-size: 22px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: right;
	line-height: 12px; }
.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
	border: none;
	border-radius: 4px;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
	color: #ffffff;
	font-size: 14px;
	font-weight: 400;
	text-transform: uppercase; }
.promo {
	min-height: 800px;
	background: url("../img/bg/car.jpeg") center center/cover no-repeat;
	padding: 150px 0 135px 0; }
.promo_header {
	color: #ffffff;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 60px;
	line-height: 60px;
	margin: 0;
	text-align: center; }
.promo_subheader {
	color: #ffffff;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 36px;
	line-height: 36px;
	margin: 0;
	margin-top: 22px;
	text-align: center; }
.promo_descr {
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	line-height: 24px;
	text-align: center;
	margin-top: 23px; }
.promo_btn {
	display: block;
	margin: 118px auto 0 auto;
	width: 259px;
	height: 63px;
	box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
	border: none;
	border-radius: 4px;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	text-transform: uppercase;
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21); }
.reasons {
	padding: 71px 0 65px 0;
	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.reason_round {
			top: 0px;
			left: 0px;
			align-items: center; }
		.reasons_descr {
			max-width: 343px;
			.reasons_subtitle {
				margin-left: 25px;
				font-weight: 400;
				font-size: 17px;
				line-height: 21px;
				color: #1eacc7; }
			.reasons_text {
				margin-left: 25px;
				align-self: center;
				color: #202020;
				font-weight: 300;
				font-size: 15px;
				line-height: 21px; } } } }
.label {
	position: relative;
	margin: 0 auto;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-align: center;
	text-transform: uppercase;
	background-color: #000000;
	padding: 6px 12px;
	width: 115px;
	height: 27px;
	z-index: 2; }
.label_white {
	background-color: #fff;
	color: #000; }
.title {
	position: relative;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	line-height: 48px;
	text-align: center;
	text-transform: uppercase;
	margin-top: 32px;
	margin-bottom: 32px;
	z-index: 2; }
.title_white {
	color: #fff; }
.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2; }
.subtitle_white {
	color: #ffffff; }
.mobile {
	min-height: 864px;
	padding: 46px 0 80px 0;
	background: url("../img/bg/mobile_bg.png") center center/cover no-repeat;
	.row {
		margin-top: 32px; } }
.mobile_item {
	position: relative;
	width: 320px;
	height: 280px;
	margin: 0 auto;
	margin-bottom: 19px;
	padding-top: 92px; }
.mobile_item_1 {
	background: url("../img/bg/mobile_screen_1.jpg") center center/cover no-repeat; }
.mobile_item_2 {
	background: url("../img/bg/mobile_screen_2.jpg") center center/cover no-repeat; }
.mobile_item_3 {
	background: url("../img/bg/mobile_screen_3.jpg") center center/cover no-repeat; }
.mobile_item_4 {
	background: url("../img/bg/mobile_screen_4.jpg") center center/cover no-repeat; }
.mobile_item_5 {
	background: url("../img/bg/mobile_screen_5.jpg") center center/cover no-repeat; }
.mobile_item_6 {
	background: url("../img/bg/mobile_screen_6.jpg") center center/cover no-repeat; }
.mobile_item_subtitle {
	color: #ffffff;
	font-size: 24px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: center; }
.mobile_item_plus {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: 35px;
	width: 26px;
	height: 26px;
	background-color: #000000;
	margin: 0 auto;
	border-radius: 100%;
	cursor: pointer; }
.choice {
	position: relative;
	background: url("../bg/blacktop.jpg") center center/cover no-repeat;
	padding: 57px 0 138px 0;
	min-height: 895px;
	.uber_img {
		position: relative;
		display: block;
		margin: 54px auto 0 auto;
		z-index: 2; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; }
	.textend {
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		text-align: center;
		color: #222; } }
